// ----- МОДАЛЬНОЕ ОКНО
.backdrop {
  @include positioning(fixed);
  left: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  height: 100%;

  background: rgba(0, 0, 0, 0.2);

  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  @include transitions(opacity, pointer-events, visibility);

  &.is-hidden {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
  }

  @media screen and (max-wodth: 767px) {
    padding: 60px 20px;
  }

  &__modal {
    @include positioning(relative);

    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    width: calc(100% - 30px);

    overflow: auto;

    padding: 40px;

    background: getColor('global-bacground');

    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;

    // --------- Tablet
    @include tablet() {
      @include positioning(absolute);
      width: 450px;
    }

    // --------- Desktop
    @include desktop() {
      padding: 40px;
      width: 528px;
    }
  }
}
