.header {
  background-color: #ffb8ca;
  padding: 8px 20px;
}

.header > .container {
  width: 100%;
  max-width: 480px;
  padding: 0;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__mob-btn {
  background-color: transparent;
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid transparent;
  margin-left: auto;
  transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
  padding: 0;

  &-icon {
    stroke: #d41443;
  }
  &:hover &-icon,
  &:focus &-icon {
    stroke: #ffffff;
    outline: transparent;
  }
}

.mob-menu__close-btn {
  background-color: transparent;
  display: block;
  border: 1px solid transparent;
  margin-left: auto;
  position: absolute;
  z-index: 2;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  stroke: #ffffff;
  padding: 0;

  &:hover,
  &:focus {
    stroke: #d41443;
    outline: transparent;
  }
}

.mob-menu-wrap {
  margin: 0 auto;
  width: 100%;
  max-width: 480px;
}

.mob-menu {
  position: fixed;
  z-index: 2;
  background-color: #e17992;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 60px 20px;

  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .mob-menu-wrap {
    display: flex;
    flex-direction: column;

    height: 100%;
    overflow: auto;
  }

  @media screen and (min-width: 768px) {
    display: none;
  }

  @media screen and (min-width: 1200px) {
    display: none;
  }
}

.mob-menu-list__item {
  padding-top: 11px;
  padding-bottom: 13px;
  border-top: 1px solid #e18298;
  border-bottom: 1px solid #e18298;
}

.mob-menu-list__link {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  &:hover,
  &:focus {
    color: #d41443;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
}
